@import url(https://fonts.googleapis.com/css?family=Pacifico|Arvo);
*{
    margin: 0;
}
.app_header{
    padding: 5px 60px 5px 100px;
    border-bottom: 1px solid rgba(128, 128, 128, 0.322);
    font-family: Pacifico;
    font-weight: lighter;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: -webkit-sticky;
    position: sticky;
    top:0;
    z-index: 1;
}

.form{
    display: flex;
    flex-direction: column;
}

h3{
    text-align: center;
    font-family: 'Arvo';
}

h4{
    font-family: 'Arvo';
    margin-right:15px;
}

.status{
    display: flex;
    align-items: center;
}

@media only screen and (max-width:710px){
    .app_header{
        padding: 5px 5px 5px 10px;
    }
}
.post{
    width:50%;
    margin: 0 auto;
    margin-top: 25px;
    margin-bottom:5px;
    border:1px solid rgba(128, 128, 128, 0.322);
    background-color: white;
}

.post_user{
    padding:20px;
    color:rgba(0, 0, 0, 0.719);
    display: flex;
    align-items: center;
}

.username{
    margin-left:10px;
}

.post_img{
    width:100%;
    border-top: 1px solid rgba(128, 128, 128, 0.322);
    object-fit: contain;
}

.post_caption{
    border-top: none;
    padding:10px;
    color: rgba(0, 0, 0, 0.808);
}

.post_comments{
    padding: 10px;
    font-family: sans-serif;
    font-size: 0.95em;
}

.post_comments p:first-child{
    margin-bottom:15px;
    color:gray;
}

.comment{
    display : flex;
    margin-bottom:8px;
}

.comment span{
    margin-left:7px;
}

.comment_form{
    display:flex;
    width: 100%;
}

.comment_box{
    border-top:1px solid rgba(128, 128, 128, 0.13);
    margin-top: 10px;
}

.input_comment{
    flex:1 1;
    padding:16px;
    border: none;
    outline:none;
}

.post_comment{
    flex:0 1;
    padding:18px;
    border: none;
    outline:none;
    background-color:white;
    color:rgb(10, 125, 170);
    cursor: pointer;
}

@media only screen and (max-width:710px){
    .post{
        width:92%;
    }
}
h3{
    text-align: center;
    font-family: 'Arvo';
}

.camera{
    transform: scale(1.3);
}

.main{
    width:30%;
}

.app{
    background-color: rgba(245, 245, 245, 0.226);
}

.upload{
    position: fixed !important;
    top: 85vh;
    left:92%;
}

@media only screen and (max-width:710px){
    .upload{
        top: 85vh;
        left:84%;
    }
}

@media only screen and (max-width:400px){
    .upload{
        top: 81vh;
        left:79%;
    }
}
